export default function shadeColor(color, percent) {
  const {r, g, b} = parseHexRgb(color);
  return formatHexRgb({
    r: mapValue(r, percent),
    g: mapValue(g, percent),
    b: mapValue(b, percent)
  });
}

function mapValue(value, percent) {
  const t = percent < 0 ? 0 : 255;
  const p = percent < 0 ? percent * -1 : percent;
  return Math.round((t - value) * p) + value;
}

function parseHexRgb(cssColor) {
  const rgb = parseInt(cssColor.slice(1), 16);
  return {
    r: rgb >> 16,
    g: (rgb >> 8) & 0x00ff,
    b: rgb & 0x0000ff
  };
}

function formatHexRgb({r, g, b}) {
  return `#${(0x1000000 + r * 0x10000 + g * 0x100 + b)
      .toString(16)
      .slice(1)}`;
}
