import React from "react";
import PropTypes from "prop-types";
import {Typography, withStyles} from "@material-ui/core";

import shadeColor from "../../../shared/functions/shadeColor";

const styles = theme => ({
  iconWrapper: {
    borderRadius: theme.shape.borderRadius,
    textAlign: "center",
    display: "inline-flex",
    alignItems: "center",
    justifyContent: "center",
    marginBottom: theme.spacing(3),
    padding: theme.spacing(1) * 1.5
  }
});

function TechnologyCard({classes, Icon, color, headline, text}) {
  return <>
    <div className={classes.iconWrapper} style={{
      color: color,
      backgroundColor: shadeColor(color, 0.5),
      fill: color
    }}>
      {Icon}
    </div>
    <Typography variant="h5" paragraph>{headline}</Typography>
    <Typography variant="body1" color="textSecondary">{text}</Typography>
  </>;
}

TechnologyCard.propTypes = {
  classes: PropTypes.object.isRequired,
  Icon: PropTypes.element.isRequired,
  color: PropTypes.string.isRequired,
  headline: PropTypes.string.isRequired,
  text: PropTypes.string.isRequired
};

export default withStyles(styles, {withTheme: true})(TechnologyCard);
