import React, {useEffect} from "react";
import PropTypes from "prop-types";
import classNames from "classnames";
import {Box, Grid, isWidthUp, withStyles, withWidth} from "@material-ui/core";
import BlogCard from "./BlogCard";

const styles = theme => ({
  blogContentWrapper: {
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
    [theme.breakpoints.up("sm")]: {
      marginLeft: theme.spacing(4),
      marginRight: theme.spacing(4)
    },
    maxWidth: 1280,
    width: "100%"
  },
  wrapper: {
    minHeight: "60vh"
  },
  noDecoration: {
    textDecoration: "none !important"
  }
});

function sizeByWidth(width) {
  if (isWidthUp("md", width)) {
    return {rows: 3, xs: 4};
  }
  if (isWidthUp("sm", width)) {
    return {rows: 2, xs: 6};
  }
  return {rows: 1, xs: 12};
}

function getVerticalBlogPosts(width, blogPosts) {
  const gridRows = [[], [], []];
  let {rows, xs} = sizeByWidth(width);
  blogPosts.forEach((blogPost, index) => {
    gridRows[index % rows].push(
      <Grid key={blogPost.id} item xs={12}>
        <Box mb={3}>
          <BlogCard src={blogPost.imageSrc} title={blogPost.title} snippet={blogPost.snippet} date={blogPost.date} url={blogPost.url}/>
        </Box>
      </Grid>
    );
  });
  return gridRows.map((element, index) => (
    <Grid key={index} item xs={xs}>
      {element}
    </Grid>
  ));
}

function Blog(props) {
  const {classes, width, blogPosts, selectBlog} = props;

  useEffect(() => {
    selectBlog();
  }, [selectBlog]);

  return (
    <Box
      display="flex"
      justifyContent="center"
      className={classNames(classes.wrapper, "lg-p-top")}
    >
      <div className={classes.blogContentWrapper}>
        <Grid container spacing={3}>
          {getVerticalBlogPosts(width, blogPosts)}
        </Grid>
      </div>
    </Box>
  );
}

Blog.propTypes = {
  selectBlog: PropTypes.func.isRequired,
  classes: PropTypes.object.isRequired,
  width: PropTypes.string.isRequired,
  blogposts: PropTypes.arrayOf(PropTypes.object)
};

export default withWidth()(withStyles(styles, {withTheme: true})(Blog));
